import React, { Component } from 'react';
import { Card, CardBody, CardHeader, Col, Row, Button, Input, Form, FormGroup, Label, Modal, ModalHeader, ModalBody, ModalFooter} from 'reactstrap';
import { toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import commonService from '../../../core/services/commonService';
import Loader from '../../Loader/Loader';
import RegisteredCasesData from './RegisteredCasesData';
import './RegisteredCases.css'
const queryString = require('query-string');
class RegisteredCases extends Component {
  constructor(props){
    super(props);
    this.state = {
      modal: false,      
      EmployeeList: [],
      organizationList: [],
      caseList: [],
      loading: true,
      formProccessing: false,
      rowIndex: -1,
      filterItem: { filter_organization_id: '', country: '', state: '', custom_search: ''},
      currentCaseInfo: {},
      assignUserId: "",
      errors: {}
    } 
    
    this.handleDeleteCases = this.handleDeleteCases.bind(this);
    this.submitHandler = this.submitHandler.bind(this);
    this.filterCasesList = this.filterCasesList.bind(this);
    this.resetSearchFilter = this.resetSearchFilter.bind(this);
    this.changeRegisteredInvestigator = this.changeRegisteredInvestigator.bind(this);
    
  }
  
  // Fetch the Employee List
  componentDidMount() { 
    
    const queryParams = queryString.parse(this.props.location.search);
    if( queryParams.organizationId !== undefined) {     
      let filterItem = this.state.filterItem;
      filterItem.filter_organization_id = queryParams.organizationId;
      this.setState({filterItem: filterItem});
    }
    this.EmployeeList(this.state.filterItem);
    this.registeredCasesList(this.state.filterItem);
  }
  /*Employee List API*/
  EmployeeList(filterItem={}) {
    let stroreWalkQuery = "?roleType=company_user";
    if(filterItem.filter_organization_id !== undefined && filterItem.filter_organization_id !== "" ) 
      stroreWalkQuery += (stroreWalkQuery !=="" ) ? "&organizationId="+filterItem.filter_organization_id: "?organizationId="+filterItem.filter_organization_id;
    if(filterItem.custom_search !== undefined && filterItem.custom_search !== "" ) 
      stroreWalkQuery += (stroreWalkQuery !=="" ) ? "&keyword="+filterItem.custom_search: "?keyword="+filterItem.custom_search;
    this.setState( { loading: true}, () => {
      commonService.getAPIWithAccessToken('employee'+stroreWalkQuery)
        .then( res => {
          
           
          if ( undefined === res.data.data || !res.data.status ) {
            this.setState( { loading: false } );
            toast.error(res.data.message);
            return;
          }   

          this.setState({loading:false, EmployeeList: res.data.data});     
         
        } )
        .catch( err => {         
          if(err.response !== undefined && err.response.status === 401) {
            localStorage.clear();
            this.props.history.push('/login');
          }
          else {
            this.setState( { loading: false } );
            toast.error(err.message);
          }
        } )
    } )
  }

  registeredCasesList(filterItem={}) {
    
    let stroreWalkQuery = "?isSaved=true";
    if(filterItem.filter_organization_id !== undefined && filterItem.filter_organization_id !== "" ) 
      stroreWalkQuery += (stroreWalkQuery !=="" ) ? "&organizationId="+filterItem.filter_organization_id: "?organizationId="+filterItem.filter_organization_id;
    if(filterItem.employee_id !== undefined && filterItem.employee_id !== "" ) 
      stroreWalkQuery += (stroreWalkQuery !=="" ) ? "&searchBYEmployee="+filterItem.employee_id: "?searchBYEmployee="+filterItem.employee_id;
    
    if(filterItem.custom_search !== undefined && filterItem.custom_search !== "" ) 
      stroreWalkQuery += (stroreWalkQuery !=="" ) ? "&keyword="+filterItem.custom_search: "?keyword="+filterItem.custom_search;
    if(filterItem.status !== undefined && filterItem.status !== "" ) 
      stroreWalkQuery += (stroreWalkQuery !=="" ) ? "&status="+filterItem.status: "?status="+filterItem.status;
    
    if(filterItem.caseId !== undefined && filterItem.caseId !== "" ) 
      stroreWalkQuery += (stroreWalkQuery !=="" ) ? "&caseId="+filterItem.caseId: "?caseId="+filterItem.caseId;
    this.setState( { loading: true}, () => {
      commonService.getAPIWithAccessToken('cases'+stroreWalkQuery)
        .then( res => {
          
           
          if ( undefined === res.data.data || !res.data.status ) {
            this.setState( { loading: false } );
            toast.error(res.data.message);
            return;
          }   

          this.setState({loading:false, caseList: res.data.data});     
         
        } )
        .catch( err => {         
          if(err.response !== undefined && err.response.status === 401) {
            localStorage.clear();
            this.props.history.push('/login');
          }
          else {
            this.setState( { loading: false } );
            toast.error(err.message);
          }
        } )
    } )
  }

  
  filterCasesList(){
    const filterItem = this.state.filterItem;
    this.registeredCasesList(filterItem);
  }
  

  changeFilterHandler = event => {
    const name = event.target.name;
    const value = event.target.value;
    const filterItem = this.state.filterItem
    filterItem[name] = value;
    this.setState({ filterItem: filterItem });
  };
  
  

  
  /* Delete Casses*/
  handleDeleteCases(rowIndex){
   
    const caseInfo = this.state.caseList[rowIndex];
    
    this.setState( { loading : true}, () => {   
      commonService.deleteAPIWithAccessToken('cases', {caseId: caseInfo.caseId})
      .then( res => {
        
         
        if ( undefined === res.data.data || !res.data.status ) {
         
          this.setState( { loading : false} );
          toast.error(res.data.message);
          return;
        } 
        
        this.setState({ modal: false, loading : false});
        toast.success(res.data.message);
        this.registeredCasesList(this.state.filterItem);
       
      } )
      .catch( err => {         
        if(err.response !== undefined && err.response.status === 401) {
          localStorage.clear();
          this.props.history.push('/login');
        }
        else
          this.setState( { loading : false } );
          toast.error(err.message);
      } )
    } );
   
    
  }
 
  resetSearchFilter() {
    this.setState({filterItem: {filter_organization_id: "", custom_search: "", employee_id: "", status:"", caseId: "" }});
    this.registeredCasesList();
  }

  changeRegisteredInvestigator(rowIndex) {
    const caseInfo = this.state.caseList[rowIndex];
    this.setState({modal: true, rowIndex: rowIndex, currentCaseInfo: caseInfo, assignUserId: caseInfo.assignUserId || "" });
  }

  /*Changes Company User*/
  /* Submit Form Handler*/
  submitHandler (event) {
    event.preventDefault();
    event.target.className += " was-validated";
    if(this.state.assignUserId === undefined || this.state.assignUserId === ""){
      let errors = {assignUserId: "Please select company user"};
      this.setState({errors: errors});
      return false;
    }

    this.setState( { formProccessing: true, errors: {}}, () => {
      
      const rowIndex = this.state.rowIndex;
      const caseInfo = this.state.caseList[rowIndex];
      commonService.postAPIWithAccessToken('cases/assign-company-user', {caseId: caseInfo.caseId, assignUserId: this.state.assignUserId})
        .then( res => {           
          if ( undefined === res.data.data || !res.data.status ) { 
            this.setState( { formProccessing: false} );
            toast.error(res.data.message);
            return;
          } 
          this.setState({ modal: false, formProccessing: false});
          toast.success(res.data.message);
          this.registeredCasesList(this.state.filterItem);        
        } )
        .catch( err => {         
          if(err.response !== undefined && err.response.status === 401) {
            localStorage.clear();
            this.props.history.push('/login');
          }else{
            this.setState( { formProccessing: false } );
            toast.error(err.message);
          }
        } )
      
    } );
    
  };

  toggle = () => {
    this.setState({
      modal: !this.state.modal,
      rowIndex: -1,
      currentCaseInfo: {},
      assignUserId: ""
    });
  }
  render() {

    const {  loading, caseList, modal, formProccessing, EmployeeList, errors} = this.state;     
    let loaderElement = '';
    if(loading)        
      loaderElement = <Loader />
    const processingBtnText = <>Save Changes <i className="fa fa-spinner"></i></>;

    return (
      <div className="dashboard-section">
       {loaderElement}
        <Card className="vd-card">
          <CardHeader>
            <div className="d-flex align-items-center">
              <div className="mr-auto">
                <h4 className="card-title"><i class="fa fa-file-text" aria-hidden="true"></i> Manage Registered Cases</h4>
              </div>
              
            </div>
          </CardHeader>
          <CardBody className="item-list-section">
            <div className="Search-filter">
              <div className="row">
                  
                  
                  
                  <Col md={"3"}>
                    <FormGroup> 
                      <Label htmlFor="filter_organization_id">Search By Case ID</Label>            
                      <Input type="number" placeholder="Search By Case ID" id="caseId" name="caseId" value={this.state.filterItem.caseId} onChange={this.changeFilterHandler} />
                    </FormGroup>  
                  </Col>
                  <Col md={"3"}>
                    <FormGroup> 
                      <Label htmlFor="filter_organization_id">Search By Case Title</Label>            
                      <Input type="text" placeholder="Search By Case Title" id="custom_search" name="custom_search" value={this.state.filterItem.custom_search} onChange={this.changeFilterHandler} />
                    </FormGroup>  
                  </Col>
                  <Col md={"3"}>
                    <FormGroup> 
                      <Label htmlFor="filter_doctor_id">Search By Status</Label>            
                      <Input type="select" placeholder="Search By Status" id="status" name="status" value={this.state.filterItem.status} onChange={this.changeFilterHandler}>
                          <option value = "">Select</option>
                          <option value="1">New</option>
                          <option value="2">Investigation in progress</option>
                          <option value="3">Closed</option>
                      </Input>
                    </FormGroup>  
                  </Col>
                  <Col md={"1"} className="p-0">
                    <FormGroup> 
                      <Label>&nbsp;</Label>
                      <Button color=" search-c-change" type="button" size="lg" onClick={this.filterCasesList}><i className="fa fa-search"></i></Button>&nbsp;
                      <Button color=" reset-c-change" type="reset" size="lg" onClick={this.resetSearchFilter}><i className="fa fa-refresh"></i></Button>
                    </FormGroup>             
                  </Col>
              </div>
          </div>
            <div className="card-table table-responsive">
              <RegisteredCasesData data={caseList} deleteCasesAction={this.handleDeleteCases} changeRegisteredInvestigatorAction = {this.changeRegisteredInvestigator} dataTableLoadingStatus = {this.state.loading} />
                  
            </div>
          </CardBody>
        </Card>
        <Modal isOpen={modal} toggle={this.toggle} className="full-width-modal-section organization-modal">
          <ModalHeader toggle={this.toggle}>Assign Company User</ModalHeader>
          <Form onSubmit={this.submitHandler} noValidate>
            <ModalBody>
              
              <Row>
                <Col md={"12"}>
                  <Label>Current Assign Company User: {this.state.currentCaseInfo.assignUserName}</Label>
                </Col>
                <Col md={"12"}>
                  <FormGroup>
                    <Label>Assign Company User</Label>
                    <Input type="select" placeholder="Company User*" id="assignUserId" name="assignUserId" value={this.state.assignUserId} onChange={(event) => {this.setState({assignUserId: event.target.value})}} required >
                      <option value="">Select</option>
                      {EmployeeList.map((organizationInfo, index) =>
                        <SetOrganizationDropDownItem key={index} organizationInfo={organizationInfo} />
                      )}
                    </Input>
                    <div className="invalid-feedback">
                      {errors['assignUserId']}
                    </div>
                  </FormGroup>
                </Col>
              </Row>           
            </ModalBody>
            <ModalFooter>
              
              <Button color="primary" disabled={formProccessing} type="submit">{formProccessing ? processingBtnText : 'Save Changes' }</Button>
              <Button color="secondary" onClick={this.toggle}>Cancel</Button>
            </ModalFooter>
          </Form>
        </Modal>
      </div>

    )
  }
}

function SetOrganizationDropDownItem (props) {
  const organizationInfo = props.organizationInfo;
  return (<option value={organizationInfo.authId} >{organizationInfo.firstName} {organizationInfo.lastName}</option>)
}
export default RegisteredCases;
