import React, { Component } from 'react';
//import  { Link } from 'react-router-dom';
import MUIDataTable from "mui-datatables";
//import commonService from '../../../core/services/commonService';
const getStatus = (status) => {
  let statusText = "Pending"
  if(status === 0)
    statusText = "Pending";
  else if(status === 1)
    statusText = "Approved";
  else if(status === 2)
    statusText = "UnApproved";
  return statusText;
}
class MileageData extends Component {
  
  constructor(props){
    super(props);   
    this.state = {
      buttonProcessing: false,
      rowIndex: '',
      dataTableItem: []
    };
    
  }
  componentDidMount() {   
  }
  

  deleteRegisteredCaseItem(rowIndex){    
    this.props.deleteCasesAction(rowIndex);
  }

  changeRegisteredCaseItemInvestigator(rowIndex){
    this.props.changeRegisteredInvestigatorAction(rowIndex);
  }

  render() {
    
    let rowsItem = [];    
    for(const [i, casesInfo] of this.props.data.entries()){
      console.log(i);

      let orgInfo = {   
        mileageId: casesInfo.mileageId,      
        flowType: `${casesInfo.flowType}`,
        tripDate: casesInfo.tripDate,
        tripStartDate: casesInfo.tripStartDate,
        tripEndDate: casesInfo.tripEndDate,
        fromAddress: casesInfo.fromAddress,
        toAddress: casesInfo.toAddress,
        standardMileageRate: casesInfo.standardMileageRate,
        milesDrivenMiles: casesInfo.milesDrivenMiles,
        parkingCharge: casesInfo.parkingCharge,
        tollCharge: casesInfo.tollCharge,
        totalExpense: casesInfo.totalExpense,
        employeeName: casesInfo.employeeName || " ",
        status: getStatus(casesInfo.status),   
        action: "",       
      }      
      rowsItem.push(orgInfo);
    }      
    
    const columns = [ 
      {
        label: 'Company User',
        name: 'employeeName',
      }, 
      {
        label: 'Flow Type',
        name: 'flowType',
      },     
      
      {
        label: 'Trip Date',
        name: 'tripDate',
      },
      {
        label: 'Trip Start Date',
        name: 'tripStartDate',
      },
      {
        label: 'Trip End Date',
        name: 'tripEndDate',
      },
      {
        label: 'From Address',
        name: 'fromAddress',
      },
      
      {
        label: 'To Address',
        name: 'toAddress',
      },
      {
        label: 'Standard Mileage Rate',
        name: 'standardMileageRate',
      },
      {
        label: 'Driven Miles',
        name: 'milesDrivenMiles',
      },
      {
        label: 'Parking Charge',
        name: 'parkingCharge',
      },
      {
        label: 'Toll Change',
        name: 'tollCharge',
      },
      {
        label: 'Total Expense',
        name: 'totalExpense',
      },
      {
        label: 'Status',
        name: 'status',
      },
      {
        label: 'Action',
        name: 'action',
        options: {
          filter: true,
          customBodyRender: (value, tableMeta, updateValue) => {
            let i = tableMeta.rowIndex;
            //let rowsItem = this.props.data[i] 
            return (
             <p>
              
              <button className="btn-edit" disabled={this.state.buttonProcessing} onClick={() => {if( window.confirm('Are you sure you wish to change status?'))
              this.changeRegisteredCaseItemInvestigator(i)}}><i className="fa fa-user"></i></button>
              <button className="btn-delete" disabled={this.state.buttonProcessing} onClick={() => {if( window.confirm('Are you sure you wish to delete this expense?'))
              this.deleteRegisteredCaseItem(i)}}><i className="fa fa-trash"></i></button></p>
            );
          },
        }
      },
    ];
    const options = {
      search: true,viewColumns: false,
      filter: false,
      searchOpen: false,
      print: false,
      download: true,
      responsive: 'stacked',
      selectableRows: 'none',
      textLabels: {
        body: {
          noMatch: this.props.dataTableLoadingStatus ? "Processing........" : "",
          toolTip: "Sort",
          columnHeaderTooltip: column => `Sort for ${column.label}`
        },
      },
      fixedHeaderOptions: { xAxis: false, yAxis: false }

    };
    
    return (
      <MUIDataTable
        title={"Mileage"}
        data={rowsItem}
        columns={columns}
        options={options}
      />
    );
  }
}

export default MileageData;