import React, { Component } from 'react';
import {Link} from 'react-router-dom';
import { Navbar, Collapse, Nav, NavItem} from 'reactstrap';

import './UserNavbar.css';


class UserNavbar extends Component {

  constructor(props) {
    super(props);

    this.toggle = this.toggle.bind(this);
    this.state = {
      isOpen: true,
      activeTab: '1',
      isLoggedOut: false,
    };
  }

  toggle(tab) {
    if (this.state.activeTab !== tab) {
      this.setState({
        isOpen: true,
        activeTab: tab,
      });
    }
  }
  logoutUser() {
    localStorage.clear();
      this.setState({isLoggedOut:true});
  };

  render() {

    return (
      <div className="user-navbar">
        <Navbar className="" expand="md">
        <Collapse isOpen={this.state.isOpen} navbar>
          <Nav navbar className="user-widget-list">
          
            <NavItem>
              <Link to={  "/user/dashboard" } className="nav-link">
                <i className="fa fa-dashboard"></i>
                <span className="value-orders">Dashboard</span>
              </Link>
            </NavItem>
            <NavItem>
              <Link to="/user/company-user" className="nav-link">
                <i className="fa fa-group"></i>
                <span className="value-orders">Manage Company User</span>
              </Link>
            </NavItem>

            <NavItem>
              <Link to="/user/company-employee" className="nav-link">
                <i className="fa fa-user" aria-hidden="true"></i>
                <span className="value-orders">Manage Employee User</span>
              </Link>
            </NavItem>
            <NavItem>
              <Link to="/user/registered-cases" className="nav-link">
              <i className="fa fa-file-text" aria-hidden="true"></i>
                <span className="value-orders">Manage Registered Cases</span>
              </Link>
            </NavItem>
            <NavItem>
              <Link to="/user/expense" className="nav-link">
                <i className="fa fa-money"></i>
                <span className="value-orders">Manage Expense</span>
              </Link>
            </NavItem>
            <NavItem>
              <Link to="/user/mileage" className="nav-link">
                <i className="fa fa-money"></i>
                <span className="value-orders">Manage Mileage</span>
              </Link>
            </NavItem>

          </Nav>
          </Collapse>
        </Navbar>
    </div>
    );
  }
}

export default UserNavbar;
