import React, { Component } from 'react';
import { Card, CardBody, CardHeader, Col, Row,  Label} from 'reactstrap';
import { toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import commonService from '../../../core/services/commonService';
import Loader from '../../Loader/Loader';
import Doc from '../../PDF/DocService';
import PdfContainer from '../../PDF/PdfContainer';
import './RegisteredCasesDetails.css';
class RegisteredCasesDetails extends Component {
  constructor(props){
    super(props);
    this.state = {
      loading: true,
      registeredCasesInfo: {threatType: [], howThreaten: [], modeOfThreat: [], dateOfIncident: [], incidentSupportDocs: [], companySupportDocs: [], instigatorProfile: [], victimProfile: [], othersProfile:[], resource: []}
    } 
    
    
    
  }
  
  
  componentDidMount() { 
    
    const { match: { params } } = this.props;
    this.setState( { loading: true}, () => {
      commonService.getAPIWithAccessToken('cases/'+params.registredCaseId)
        .then( res => {
          console.log(res);
          
          if ( undefined === res.data.data || !res.data.status ) {
            this.setState( {  loading: false } );
            toast.error(res.data.message);  
            this.props.history.push('/user/registered-cases');  
            return;
          }   

          this.setState({loading:false, registeredCasesInfo: res.data.data});     
         
        } )
        .catch( err => {         
          if(err.response !== undefined && err.response.status === 401) {
            localStorage.clear();
            this.props.history.push('/login');
          }
          else {
            this.setState( { loading: false } );
            toast.error(err.message);    
          }
        } )
    } )
  }

  createPdf = (html) => { 
    //window.postMessage(JSON.stringify({pdfUrl: ""}), '*');
    Doc.createPdf(html, this.state.registeredCasesInfo.caseTitle.toLowerCase()+'_'+this.state.registeredCasesInfo.caseAutoId+'_'+commonService.getCurrentDate()); 
  }
  
  render() {

    const { registeredCasesInfo, loading} = this.state;     
    let loaderElement = '';
    if(loading)   
      loaderElement = <Loader />
    
    return (
      <div className="dashboard-section">
        {loaderElement}
        <Card className="vd-card">
          <CardHeader>
            <div className="d-flex align-items-center">
              <div className="mr-auto">
                <h4 className="card-title"><i class="fa fa-file-text-o" aria-hidden="true"></i> Registered Case Details</h4>
              </div>
              
            </div>
          </CardHeader>
          

            <CardBody className="item-list-section">
              <div className="Search-filter">
                <PdfContainer createPdf={this.createPdf}  > 
                  <Row>
                    <Col md={6}><Label><strong>Case Id: </strong></Label><span> {registeredCasesInfo.caseAutoId || ''}</span></Col>
                    <Col md={6}><Label><strong>Registred By: </strong></Label><span> {registeredCasesInfo.employeeName || 'N/A' }</span></Col>
                    <Col md={6}><Label><strong>Case Type: </strong></Label><span> {registeredCasesInfo.caseTypeName || 'N/A'}</span></Col>
                    <Col md={6}><Label><strong>Case Title: </strong></Label><span> {registeredCasesInfo.caseTitle || ''}</span></Col>
                    <Col md={6}><Label><strong>Secondary to report threat: </strong></Label><span> {registeredCasesInfo.secondaryReportThreat || ''}</span></Col>
                    <Col md={6}><Label><strong>Threat Type: </strong></Label><span> {Array.isArray(registeredCasesInfo.threatType) ?  registeredCasesInfo.threatType.join(',') : ""}</span></Col>
                    <Col md={12}><hr /></Col>
                    <Col md={6}><Label><strong>How Threaten: </strong></Label><span> {registeredCasesInfo.howThreaten}</span></Col>
                    <Col md={6}><Label><strong>Mode of Threat: </strong></Label><span> {registeredCasesInfo.modeOfThreat.join(',')}</span></Col>
                    <Col md={12}><Label><strong>Description of incident: </strong></Label><span> {registeredCasesInfo.descriptionOfIncident || ''}</span></Col>
                    <Col md={6}><Label><strong>Date of incidents: </strong></Label><span> {registeredCasesInfo.dateOfIncident.join(',')}</span></Col>
                    <Col md={12}><hr />
                      <h3>Incident Information</h3>
                    </Col>
                    <Col md={6}><Label><strong>Location: </strong></Label><span> {registeredCasesInfo.incidentLocation || 'N/A'}</span></Col>
                    <Col md={6}><Label><strong>Name: </strong></Label><span> {registeredCasesInfo.incidentPersonName || 'N/A'}</span></Col>
                    <Col md={6}><Label><strong>Number: </strong></Label><span> {registeredCasesInfo.incidentPersonNumber || 'N/A'}</span></Col>
                    <Col md={6}><Label><strong>Email: </strong></Label><span> {registeredCasesInfo.incidentPersonEmail || 'N/A'}</span></Col>
                    <Col md={12}><Label><strong>Department Information: </strong></Label><span> {registeredCasesInfo.incidentDepartmentDescription || 'N/A'}</span></Col>
                    <Col md={12}><Label><strong>Supported Documents:</strong></Label>
                      <div className="imagesPreview-section">
                        {
                          registeredCasesInfo.incidentSupportDocs.map((docs, index) => 
                             <div className="imagesPreview" key={index}>
                               <a href= {docs}  target ="_blank" rel="noopener noreferrer">Preview Docs</a>
                             </div>
                          )
                        }
                      </div>
                    </Col>
                    <Col md={12}><hr />
                      <h3>Involved Parties</h3>

                      
                    </Col>
                    <Col md={12}>
                      <h5>Instigator</h5>

                      
                    </Col>
                    <Col md={12}>
                      {
                          registeredCasesInfo.instigatorProfile.map((info, index) => 
                             <Row className="instigator-info" key={index}>
                                 <Col md={4}><Label><strong>Name: </strong></Label><span> {info.instigatorName || 'N/A'}</span></Col>
                                 <Col md={4}><Label><strong>Contact: </strong></Label><span> {info.instigatorContact || 'N/A'}</span></Col>
                                 <Col md={4}><Label><strong>Date Of Birth: </strong></Label><span> {commonService.getDate(info.instigatorDOB)}</span></Col>
                                 <Col md={4}><Label><strong>Address: </strong></Label><span> {info.instigatoraddress || 'N/A'}</span></Col>
                                 <Col md={4}><Label><strong>Social Information: </strong></Label><span> {info.instigatorSocialInformation || 'N/A'}</span></Col>
                                 {info.attachFile && Array.isArray(info.attachFile) && <Col md={12}><Label><strong>Supported Documents:</strong></Label>
                                    <div className="imagesPreview-section">
                                      {
                                        info.attachFile.map((docs, index) => 
                                           <div className="imagesPreview" key={index}>
                                             <a href= {docs}  target ="_blank" rel="noopener noreferrer">Preview Docs</a>
                                           </div>
                                        )
                                      }
                                    </div>
                                  </Col> }
                             </Row>
                          )
                        }
                    </Col>
                    <Col md={12}>
                      <h5>Victim</h5>

                    </Col>
                    <Col md={12}>

                      {
                          registeredCasesInfo.victimProfile.map((info, index) => 
                             <Row className="instigator-info" key={index}>
                                 <Col md={4}><Label><strong>Name: </strong></Label><span> {info.victimName || 'N/A'}</span></Col>
                                 <Col md={4}><Label><strong>Contact: </strong></Label><span> {info.victimContact || 'N/A'}</span></Col>
                                 <Col md={4}><Label><strong>Date Of Birth: </strong></Label><span> {commonService.getDate(info.victimDOB)}</span></Col>
                                 <Col md={4}><Label><strong>Address: </strong></Label><span> {info.victimaddress || 'N/A'}</span></Col>
                                 <Col md={4}><Label><strong>Social Information: </strong></Label><span> {info.victimSocialInformation || 'N/A'}</span></Col>
                                 {info.attachFile && Array.isArray(info.attachFile) && <Col md={12}><Label><strong>Supported Documents:</strong></Label>
                                    <div className="imagesPreview-section">
                                      {
                                        info.attachFile.map((docs, index) => 
                                           <div className="imagesPreview" key={index}>
                                             <a href= {docs}  target ="_blank" rel="noopener noreferrer">Preview Docs</a>
                                           </div>
                                        )
                                      }
                                    </div>
                                  </Col> }
                             </Row>
                          )
                        }
                    </Col>
                    <Col md={12}>
                      <h5>Others</h5>

                      
                    </Col>
                    <Col md={12}>
                      {
                          registeredCasesInfo.othersProfile.map((info, index) => 
                             <Row className="instigator-info" key={index}>
                                 <Col md={4}><Label><strong>Name: </strong></Label><span> {info.othersName || 'N/A'}</span></Col>
                                 <Col md={4}><Label><strong>Contact: </strong></Label><span> {info.othersContact || 'N/A'}</span></Col>
                                 <Col md={4}><Label><strong>Date Of Birth: </strong></Label><span> {commonService.getDate(info.othersDOB)}</span></Col>
                                 <Col md={4}><Label><strong>Address: </strong></Label><span> {info.othersaddress || 'N/A'}</span></Col>
                                 <Col md={4}><Label><strong>Social Information: </strong></Label><span> {info.othersSocialInformation || 'N/A'}</span></Col>
                                 {info.attachFile && Array.isArray(info.attachFile) && <Col md={12}><Label><strong>Supported Documents:</strong></Label>
                                    <div className="imagesPreview-section">
                                      {
                                        info.attachFile.map((docs, index) => 
                                           <div className="imagesPreview" key={index}>
                                             <a href= {docs}  target ="_blank" rel="noopener noreferrer">Preview Docs</a>
                                           </div>
                                        )
                                      }
                                    </div>
                                  </Col> }
                             </Row>
                          )
                        }
                    </Col>
                    <Col md={12}>
                      <h5>Resource</h5>

                      
                    </Col>
                    <Col md={12}>
                      {
                          registeredCasesInfo.resource.map((info, index) => 
                             <Row className="instigator-info" key={index}>
                                 <Col md={4}><Label><strong>Resource Name: </strong></Label><span> {info.resourceName || 'N/A'}</span></Col>
                                 <Col md={4}><Label><strong>Contact: </strong></Label><span> {Array.isArray(info.instigatorContact) ? info.instigatorContact.join(',') : ""}</span></Col>
                                 <Col md={4}><Label><strong>Discussion Summary: </strong></Label><span> {info.discussionSummary || 'N/A'}</span></Col>
                                 {info.attachFile && Array.isArray(info.attachFile) && <Col md={12}><Label><strong>Supported Documents:</strong></Label>
                                    <div className="imagesPreview-section">
                                      {
                                        info.attachFile.map((docs, index) => 
                                           <div className="imagesPreview" key={index}>
                                             <a href= {docs}  target ="_blank" rel="noopener noreferrer">Preview Docs</a>
                                           </div>
                                        )
                                      }
                                    </div>
                                  </Col> }
                             </Row>
                          )
                        }
                    </Col>
                    <Col md={12}><hr />
                      <h3>Company Information</h3>
                    </Col>
                    <Col md={6}><Label><strong>Initial Threat Level: </strong></Label><span> {registeredCasesInfo.initialThreatLevel || '0'}</span></Col>
                    <Col md={6}><Label><strong>Supported Documents:</strong></Label></Col>
                    <Col md={12}>  <div className="imagesPreview-section">
                        {
                          registeredCasesInfo.companySupportDocs.map((docs, index) => 
                             <div className="imagesPreview" key={index}>
                               <a href= {docs}  target ="_blank" rel="noopener noreferrer">Preview Docs</a>
                             </div>
                          )
                        }
                      </div></Col>
                    <Col md={12}><hr />
                      <h3>Threat Information and Details</h3>
                    </Col>
                    <Col md={6}>
                        <Label><strong>Did a physical assault happen recently or in the past? </strong></Label>
                        
                        <p>{registeredCasesInfo.physicalAssault} { registeredCasesInfo.physicalAssault !== undefined ? registeredCasesInfo.physicalAssault === "Yes" ? "There is an extremely high probability that an attacker will repeat an attack if given an opportunity to do so if the existing threat is not mitigated. This is very prevalent in intimate partner violence." : registeredCasesInfo.physicalAssault === "No" ? "This is not a mitigating factor; you will need to review the evidence and interviews to determine if the instigator is on a pathway to targeted violence." : "You should be able to get this information from the victim. If the victim is not available, continue interviewing until a conclusion about this question is made.":""}</p>
                        <p>Description: {registeredCasesInfo.physicalAssaultDescription}</p>

                    </Col>
                    <Col md={6}>
                        <Label><strong>Is Law Enforcement involved? </strong></Label>
                        <p>{registeredCasesInfo.lawEnforcement} { registeredCasesInfo.lawEnforcement !== undefined ? registeredCasesInfo.lawEnforcement === "Yes" ? 'Be sure to get a report ID, reporting officers name and contact info along with all available documents. Law Enforcement is a great ally to gain a better understanding of your instigator, if they are a frequent flyer in their system and other concerning behaviors that were evident during the interaction.' : registeredCasesInfo.lawEnforcement === "No" ? "Determine with the victim if Law Enforcement needs to be contacted. You cannot force the victim to take this route if they are unwilling, unless you are a witness to the incident and/or it is an active situation." : "You should be able to get this information from the victim. If the victim is not available, continue interviewing until a conclusion about this question is made.": ""}</p>
                        <p>Description: {registeredCasesInfo.lawEnforcementDescription}</p>

                    </Col>
                    <Col md={6}>
                        <Label><strong>Is there a protection order place or planned? </strong></Label>
                        <p>{registeredCasesInfo.protectionOrder} { registeredCasesInfo.protectionOrder !== undefined ? registeredCasesInfo.protectionOrder === "Yes" ? "Try to determine what information is available on the order, applicable locations, temporary or permanent, victim or law enforcement initiated. All this information will be relevant when identifying other interventions and responses." : registeredCasesInfo.protectionOrder === "No" ? "Determine with the victim if Law Enforcement needs to be contacted or report made at court. You cannot force the victim to take this route if they are unwilling. The victim will need to understand what is involved with following through as some victims of intimate partner violence may be reluctant to do so." : "You should be able to get this information from the victim. If the victim is not available, continue interviewing until a conclusion about this question is made.": ""}</p>
                        <p>Description: {registeredCasesInfo.protectionOrderDescription}</p>

                    </Col>
                    <Col md={6}>
                        <Label><strong>History of violence? </strong></Label>
                        <p>{registeredCasesInfo.historyViolence} { registeredCasesInfo.historyViolence !== undefined ? registeredCasesInfo.historyViolence === "Yes" ? "Individuals with a history of violence show a greater rate of future violence, especially with intimate partner relationships. Understand the situation behind each violent encounter and be sure to identify if there were any mitigating factors that can be used in your investigation." : registeredCasesInfo.historyViolence === "No" ? "This is not a mitigating factor; you will need to review the evidence and interviews to determine if the instigator is on a pathway to targeted violence. The Secret Service reports that there are significant number of active assailant incidents by offenders with no history of violence. Remember, every violent offender has a first offense." : "You should be able to get this information from a background check and interviews with known associates. If none are available, continue interviewing until a conclusion about this question is made.": ""}</p>
                        <p>Description: {registeredCasesInfo.historyViolenceDescription}</p>

                    </Col>
                    <Col md={6}>
                        <Label><strong>History of weapons use or access to weapons? </strong></Label>
                        <p>{registeredCasesInfo.historyWeapon} { registeredCasesInfo.historyWeapon !== undefined ? registeredCasesInfo.historyWeapon === "Yes" ? "Understand the laws in your area to determine if you have probable action to take against the instigator and possible removal of weapons. Depending on the severity of your threat, you should consider using your resources to assist you with this process." : registeredCasesInfo.historyWeapon === "No" ? "" : "": ""}</p>
                        <p>Description: {registeredCasesInfo.historyWeaponDescription}</p>

                    </Col>
                    <Col md={6}>
                        <Label><strong>History of heavy alcohol use/addiction? </strong></Label>
                        <p>{registeredCasesInfo.historyAlcohol} { registeredCasesInfo.historyAlcohol !== undefined ? registeredCasesInfo.historyAlcohol === "Yes" ? "Side effects from either being drunk on alcohol or withdrawals from being off can be extremely concerning. If assault(s) happen only while instigator is under the influence, there is good cause for concern and need for intervention. Being drunk is not an acceptable cause for assault, violence is violence." : registeredCasesInfo.historyAlcohol === "No" ? "This is a positive thing and can be used as a mitigating factor in your investigation. However, just because the instigator is not known to have a problem with alcohol does not mean they will not become violent." : "You should be able to get this information from the background, victim and/or known associates. If no information is presently available, continue interviewing until a conclusion about this question is made.": ""}</p>
                        <p>Description: {registeredCasesInfo.historyAlcoholDescription}</p>

                    </Col>
                    <Col md={6}>
                        <Label><strong>Under the influence at the time of the incident(s)? </strong></Label>
                        <p>{registeredCasesInfo.influnece}</p>
                        <p>Description: {registeredCasesInfo.influenceDescription}</p>

                    </Col>

                    <Col md={6}>
                        <Label><strong>Gang or extremist affiliations? </strong></Label>
                        <p>{registeredCasesInfo.gang} { registeredCasesInfo.gang !== undefined ? registeredCasesInfo.gang === "Yes" ? "Any affiliation to violent organization is a sign for concern. The FBI says there is no single reason explains why people become violent extremists, but it often happens when someone is trying to fill a deep personal need. Some people also become violent extremists because they disagree with government policy, hate certain types of people, do not feel valued or appreciated by society, or think they have limited chances to succeed." : registeredCasesInfo.gang === "No" ? "This is a positive thing for the instigator; however, this alone is not enough to negate the concern for threats.You should be able to get this information from the background, victim and/or known associates. If no information is presently available, continue interviewing until a conclusion about this question is made." : "You should be able to get this information from the background, victim and/or known associates. If no information is presently available, continue interviewing until a conclusion about this question is made.": ""}</p>
                        <p>Description: {registeredCasesInfo.gangDescription}</p>

                    </Col>
                    <Col md={6}>
                        <Label><strong>Known specialized training (law enforcement or military)? </strong></Label>
                        <p>{registeredCasesInfo.specializedTraining} { registeredCasesInfo.specializedTraining !== undefined ? registeredCasesInfo.specializedTraining === "Yes" ? "Instigators with specialized training and skills pose an exceptional concern. Understanding the entirety of their background will ensure you apply the appropriate responses and interventions without setting off any additional triggers. Consider accessing resources for further advice." : registeredCasesInfo.specializedTraining === "No" ? "This is a positive thing for the instigator; however, this alone is not enough to negate the concern for threats. You will need to know if there are any legally or illegally owned firearms or connections to associates known to carry firearms or other weapons." : "You should be able to get this information from the background, victim and/or known associates. If no information is presently available, continue interviewing until a conclusion about this question is made. Check background addresses for former APO or FPO locations, as well as any known military installations. You will also want to question gaps in history or overseas addresses.": ""}</p>
                    </Col>
                    <Col md={6}>
                        <Label><strong>Known financial or personal problems (BK, Divorce)? </strong></Label>
                        <p>{registeredCasesInfo.financialProblem} { registeredCasesInfo.financialProblem !== undefined ? registeredCasesInfo.financialProblem === "Yes" ? "Financial problems cause people to take drastic measures. If the instigator has recently had or is in the middle of a financial concern such as a bankruptcy, civil lawsuit (either side), or late on a housing payment will escalate the concern. Depending on the severity of the threats, associated with the grievance, you may want to contact your resources for further assistance." : registeredCasesInfo.financialProblem === "No" ? "This is a positive thing for the instigator; however, this alone is not enough to negate the concern for threats." : "You should be able to get this information from the background, victim and/or known associates. If no information is presently available, continue interviewing until a conclusion about this question is made.": ""}</p>
                        

                    </Col>
                    <Col md={6}>
                        <Label><strong>Known employment issue? </strong></Label>
                        <p>{registeredCasesInfo.employmentIssue}</p>
                        

                    </Col>
                    <Col md={6}>
                        <Label><strong>Past suicidal ideation? </strong></Label>
                        <p>{registeredCasesInfo.pastSucidial} { registeredCasesInfo.pastSucidial !== undefined ? registeredCasesInfo.pastSucidial === "Yes" ? "A fascination with killing is always a concern. Whether the thoughts are about oneself or others, this is a sign of desensitization and elevates the concern for an act of violence. Depending on the specificity of the statements, you may want to consider contacting law enforcement if not already involved." : registeredCasesInfo.pastSucidial === "No" ? "This is a positive thing for the instigator; however, this alone is not enough to negate the concern for threats." : "You should be able to get this information from the victim and/or known associates. If no information is presently available, continue interviewing until a conclusion about this question is made.": ""}</p>
                        

                    </Col>
                    <Col md={12}><hr />
                      <h3>Mental Health Concerns</h3>
                    </Col>
                    <Col md={6}>
                        <Label><strong>Are you mental health provider or is this diagnosis from one? </strong></Label>
                        <p>{registeredCasesInfo.mentalHealthProvider} { registeredCasesInfo.pastSucidial !== undefined ? registeredCasesInfo.mentalHealthProvider === "Yes" ? "If you are not a mental health professional and it has either been brought to your attention that a mental health concern exists, or determination was made after threshold evaluation then it would be highly suggested to involve one. Without the proper background in mental health, threats or concerning behavior may go unnoticed or under reported.<br /> Mental health concerns always raise the level of threat until you can establish more information from an expert. <br /> If you are a mental health professional, please elaborate more on the concern, analysis, and recommendations." : registeredCasesInfo.mentalHealthProvider === "No" ? "Mental health is often thrown out as a concern with limited information to support, as a definition, it is extremely easy to label someone without an assessment, however, this could create biases in your opinion and lead to a miscalculated threat." : "If it has not been brought to your attention nor have you established a basis for a mental health concern, do not worry, not all threat cases involve a mental health concern. Please establish more information to either eliminate or validate your opinion if possible.": ""}</p>

                    </Col>
                    <Col md={6}>
                        <Label><strong>Are you psychologist? </strong></Label>
                        <p>{registeredCasesInfo.psychologist}</p>

                    </Col>
                    <Col md={6}>
                        <Label><strong>Is this diagnosis from a psychologist? </strong></Label>
                        <p>{registeredCasesInfo.diagnosisPsychologist}</p>

                    </Col>
                    <Col md={6}>
                        <Label><strong>Does instigator suffer from high paranoia? </strong></Label>
                        <p>{registeredCasesInfo.instigatorSuffer}</p>

                    </Col>
                    <Col md={6}>
                        <Label><strong>Does instigator suffer from Psychotic Violent thoughts? </strong></Label>
                        <p>{registeredCasesInfo.violentThoughts}</p>

                    </Col>
                    <Col md={6}>
                        <Label><strong>Other mental health concerns? </strong></Label>
                        <p>{registeredCasesInfo.otherMentalHealth}</p>
                        <p>{registeredCasesInfo.otherMentalHealthDescription}</p>
                    </Col>
                    <Col md={6}>
                        <Label><strong>From a scale of 1-5 please rate serverity? </strong></Label>
                        <p>{registeredCasesInfo.mentalHealthRate}</p>

                    </Col>

                    
                    <Col md={12}><hr />
                      <h3>Grivances</h3>
                    </Col>
                    <Col md={6}>
                        <Label><strong>Financial or Material </strong></Label>
                        <p>Financial and material grievances require the assessor to understand not only the value of the item, but also if there is an intrinsic value  to it as well. These grievances can be mitigated by finding a balance on what is being requested and reasonability of request. If there are no other grievances involved, this should likely be resolved. Be cautious to not create bad habits, if the subject believes they can threaten you and get paid for it, this could be a long-term problem.</p>
                        <p>{registeredCasesInfo.financial ? "Yes" : "No"}</p>
                    </Col>
                    <Col md={6}>
                        <Label><strong>Personal Prejudice (Racist/Extremist) </strong></Label>
                        <p>Racism and extremism have many avenues of concern and each have their own likelihood of actualizing. If your grievance involves this component, the assessor will need to identify which group the subject identifies with and ways to divert attention and aggression. Unless you are a mental health professional trying to help the subject, you should not try to fix the subject, you should mitigate the threat based on the available information.</p>
                        <p>{registeredCasesInfo.personal ? "Yes" : "No"}</p>
                    </Col>
                    <Col md={6}>
                        <Label><strong>Work Performance or Sabotage </strong></Label>
                        <p>Category involving work performance and potential sabotage could also make the case for financial grievance.  The specifics as to what was done will be important to understand how this threat can be mitigated. If they are still a current employee/student, be cognizant of what they have access to, a concern for physical harm could turn into an insider threat without tapered access.</p>
                        <p>{registeredCasesInfo.workPerformance ? "Yes" : "No"}</p>
                    </Col>
                    <Col md={6}>
                        <Label><strong>Intimate Relationship Fixation </strong></Label>
                        <p>These grievances are the most concerning for the victim and have the highest percentage of actualizing to violence. These concerns can include physical violence, sexual violence, threats of physical or sexual violence or psychological/emotional violence. Anticipate that any change in victim behavior could escalate the threat and might be a trigger for them to exert more control.</p>
                        <p>{registeredCasesInfo.intimateRelationship ? "Yes" : "No"}</p>
                    </Col>
                    <Col md={6}>
                        <Label><strong>Non-Intimate Relationship Fixation </strong></Label>
                        <p>This is referring to a fixation of an individual by an unknown person or someone that had not engaged in a relationship, that desires to have one. Since they are not intimate with each other there is a good chance that changes in victim behavior has a high probability of reducing or eliminating the threatening behavior. We usually see these concerns with high profile targets such as actors or business leaders, however, a simple encounter at a coffee shop could produce the same impact.</p>
                        <p>{registeredCasesInfo.nonIntimateRelationship ? "Yes" : "No"}</p>
                    </Col>
                    <Col md={6}>
                        <Label><strong>Stalking Involved </strong></Label>
                        <p>Stalking is often involved in nonintimate or intimate concerns. Stalking behavior is extremely concerning as it is usually a precursor to an approach and incident. Stalking is any harassing or threatening behavior that a subject engages in, such as following them in person or online, sending unwanted messages or gifts. Stalking and intimate partner violence my occur together. Changes in victim behavior and routines will help if nonintimate, be cautious of all surrounding information, if you dont have enough for a determination, proceed on obtaining more.</p>
                        <p>{registeredCasesInfo.stalking ? "Yes" : "No"}</p>
                    </Col>
                    <Col md={6}>
                        <Label><strong>Grivances Description </strong></Label>
                        <p>{registeredCasesInfo.grievanceDescription}</p>
                    </Col>
                    <Col md={12}><hr />
                      <h3>Victim Assessment</h3>
                    </Col>
                    <Col md={6}>
                        <Label><strong>Prior incidents of abuse? </strong></Label>
                        <p>{registeredCasesInfo.priprIncident} {registeredCasesInfo.priprIncident !== undefined ? registeredCasesInfo.priprIncident === "Yes" ? "Victims with prior incidents of abuse from an intimate partner have a higher likelihood of being a repeat victim. Regardless if it is the same partner or a new one, a yes answer should escalate your protective measures and evaluation." : registeredCasesInfo.priprIncident === "No" ? "If there is no history of abuse, you will want to understand more about the dynamics of the current concern. Intimate partner threats of violence have a higher likelihood of a physical assault taking place and much more difficult to redirect the instigators behaviors." : "This is a critical question to answer, if the victim has been previously abused, especially by the current instigator, the chances of a repeat offense grow exponentially." : ""}</p>
                    </Col>
                    <Col md={6}>
                        <Label><strong>Stable Living situation? </strong></Label>
                        <p>{registeredCasesInfo.stableLiving} {registeredCasesInfo.stableLiving !== undefined ? registeredCasesInfo.stableLiving === "Yes" ? "Where your victim rests or finds comfort is a critical component to stabilizing their response to this concern. It will be important to find a stable location or make their current situation as stable as possible." : registeredCasesInfo.stableLiving === "No" ? "An unstable living situation can lead to an escalation in behaviors from the victim and the instigator. In an intimate partner concern, this could also lead to the victim to return to the instigator in hopes of seeking security/comfort. Statistics from US Secret Service shows a high number of attacks occur by individuals with a poor home life." : "If you are unaware of their situation, you should return to the interview and follow up with a few items you may have missed. Their living situation will help you understand other stressors that may be present and influencing their behavior." : ""}</p>
                    </Col>
                    <Col md={6}>
                        <Label><strong>Employment/Education concerns? </strong></Label>
                        <p>{registeredCasesInfo.employmentConcern} {registeredCasesInfo.employmentConcern !== undefined ? registeredCasesInfo.employmentConcern === "Yes" ? "The major issue you should be concerned with are in noticeable changes in behavior. Example would be if they have been a poor performer for a while, which isnt a change in behavior and less concerning. However, if you have a good or top performer, who is now a poor performer, this should escalate your concern. Try to find the root of the concerns." : registeredCasesInfo.employmentConcern === "No" ? "Negative performance reviews in school or work are signs of concerns. work/school performance can be used a way to determine where someone is on the escalation of violence. If no changes in performance is noted, it will be extremely important to monitor this during and after the investigation. Be cautious, as these factors change quick and you will want to notate this in your records.It will be beneficial to understand the performance behaviors of your subject. Try to determine if there are any noticeable changes, specifically poor performance. You will want to inquire with someone who is familiar with their performance." : "It will be beneficial to understand the performance behaviors of your subject. Try to determine if there are any noticeable changes, specifically poor performance. You will want to inquire with someone who is familiar with their performance." : ""}</p>
                    </Col>
                    <Col md={6}>
                        <Label><strong>Current or planned security measures in place? </strong></Label>
                        <p>{registeredCasesInfo.currentSecurity} {registeredCasesInfo.currentSecurity !== undefined ? registeredCasesInfo.currentSecurity === "Yes" ? "This is an extremely important measure, not just for keeping them physically safe, but also mentally prepared.  Please notate advice given and what measures are implemented." : registeredCasesInfo.currentSecurity === "No" ? "Depending on your initial evaluation of the threat, you will want to determine weaknesses that could leave your victim(s) vulnerable. You may request that a security professional offer some guidance based on their specific scenario." : "Security measures are used to deter and change the behavior of the instigator. This is an extremely important step as it balances the concern for the victim while providing them a safe environment." : ""}</p>
                    </Col>
                    <Col md={6}>
                        <Label><strong>Security training or enhanced skills? </strong></Label>
                        <p>{registeredCasesInfo.securityTraining} {registeredCasesInfo.securityTraining !== undefined ? registeredCasesInfo.securityTraining === "Yes" ? "If you answered yes, you would need to understand the scope in which they are trained, this will help with your security measures needed to provide safety. Understand that this could also complicate protective measures and advice, please understand deficiencies involved with an overconfident victim." : registeredCasesInfo.securityTraining === "No" ? "Not everyone is trained in security or enhanced skills, but anyone can be trained to keep themselves safe. Coordinate this with your security measures to provide a balanced plan for the victim to live safely and comfortably." : "Your initial determination and victims perspective on the situation will guide this response. You may determine that nothing is available, or the victim is not taking your advice, either way, notate the results and file your log." : ""}</p>
                    </Col>
                    <Col md={6}>
                        <Label><strong>Assistance program offered? </strong></Label>
                        <p>{registeredCasesInfo.assignmentProgram} {registeredCasesInfo.assignmentProgram !== undefined ? registeredCasesInfo.assignmentProgram === "Yes" ? "If you currently partner with an assistance program at your school/work, fantastic, you are a step ahead of most. Please ensure proper guidance from your legal office is also available to navigate you through appropriate measures." : registeredCasesInfo.assignmentProgram === "No" ? "If you currently have a partner in this field, please reach out to them for guidance and what is available. If you do not have a resource, it is highly encourage to find and secure a resource to assist with these concerns. Please circle back to this section once complete and adjust assessment as needed." : "Depending on your initial assessment of the severity of the concern, this may not be necessary. However, utilizing a third party to assist with the providing support such as mental health, victims of abuse and other programs will be extremely helpful when mitigating future threats towards them." : ""}</p>
                    </Col>
                    <Col md={6}>
                        <Label><strong>Support system available(family/friends)? </strong></Label>
                        <p>{registeredCasesInfo.supportSystem} {registeredCasesInfo.supportSystem !== undefined ? registeredCasesInfo.supportSystem === "Yes" ? "A study released by the US Secret Service in 2019 identified that a positive support system is a key factor to mitigating attacks before they happen. If your victim has strong family ties, it will make the difference when you need someone close to the victim to assist in communicating necessary precautions or actions." : registeredCasesInfo.supportSystem === "No" ? "A study released by the US Secret Service in 2019 identified that a positive support system is a key factor to mitigating attacks before they happen. If there is no one available to assist, this would be a great opportunity to utilize a support system or company." : "A support system for the victim will be important to help remove them from a dangerous/volatile situation. If there is a support system, but not close by, you will wan to understand more about their living situation and why they believe they do not have support." : ""}</p>
                    </Col>
                    <Col md={6}>
                        <Label><strong>Mental Health Concerns? </strong></Label>
                        <p>{registeredCasesInfo.mentalHealth} {registeredCasesInfo.mentalHealth !== undefined ? registeredCasesInfo.mentalHealth === "Yes" ? "If you are not a mental health professional and it has either been brought to your attention that a mental health concern exists, or determination was made after threshold evaluation then it would be highly suggested to involve one. Without the proper background in mental health, threats or concerning behavior may go unnoticed or under reported. <br /> Mental health concerns always raise the level of threat until you can establish more information from an expert <br/> If you are a mental health professional, please elaborate more on the concern, hypothesis and recommendations." : registeredCasesInfo.mentalHealth === "No" ? "Mental health is often thrown out as a concern with limited information to support, as a definition, it is extremely easy to label someone without an assessment, however, this could create biases in your opinion and lead to a miscalculated threat." : "If it has not been brought to your attention nor have you established a basis for a mental health concern, do not worry, not all threat cases involve a mental health concern. Please establish more information to either eliminate or validate your opinion if possible." : ""}</p>
                    </Col>
                    <Col md={6}>
                        <Label><strong>Description of incidents? </strong></Label>
                        <p>{registeredCasesInfo.victimDescription}</p>
                    </Col>
                    {
                      registeredCasesInfo.isSaved && <><Col md={12}><hr />
                        <h3>Notes</h3>
                      </Col>
                      <Col md={12}>
                      {
                          registeredCasesInfo.caseNotes.map((info, index) => 
                             <Row className="instigator-info" key={index}>
                                 <Col md={4}><Label><strong>Notes: </strong></Label><span> {info.notes || ''}</span></Col>
                                 {info.notesSupportDocs && Array.isArray(info.notesSupportDocs) && <Col md={12}><Label><strong>Supported Documents:</strong></Label>
                                    <div className="imagesPreview-section">
                                      {
                                        info.notesSupportDocs.map((docs, index) => 
                                           <div className="imagesPreview" key={index}>
                                             <a href= {docs}  target ="_blank" rel="noopener noreferrer">Preview Docs</a>
                                           </div>
                                        )
                                      }
                                    </div>
                                  </Col> }
                             </Row>
                          )
                        }
                      </Col></>
                    }
                  </Row>
                </PdfContainer>
              </div>
            </CardBody>
          
        </Card>
        
      </div>

    )
  }
}

export default RegisteredCasesDetails;
