import React, { Component } from 'react';
import { Card, CardBody, CardHeader, Col, Row, Button, Form, Input, FormGroup, Label, Modal, ModalHeader, ModalBody, ModalFooter} from 'reactstrap';
import { toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import commonService from '../../../core/services/commonService';
import Loader from '../../Loader/Loader';
import EmployeeData from './EmployeeData';
import './Employee.css'
const queryString = require('query-string');
class Employee extends Component {
  constructor(props){
    super(props);
    this.state = {
      modal: false,      
      EmployeeList: [],
      organizationList: [],
      loading: true,
      formProccessing: false,
      rowIndex: -1,
      formField: { organizationId: '', email: '', firstName: '',lastName: '', phoneNumber: '', address: '', city: '', state: '', country: '', postalCode: '', role: '', status: '' },
      errors: { email: '', employee_name: '', role: '', error: ''},
      formValid: true,
      filterItem: { filter_organization_id: '', country: '', state: '', custom_search: ''},
    } 
    this.handleEditEmployee = this.handleEditEmployee.bind(this);
    this.submitHandler = this.submitHandler.bind(this);
    this.handleDeleteEmployee = this.handleDeleteEmployee.bind(this);
    this.filterEmployeeList = this.filterEmployeeList.bind(this);
    this.resetSearchFilter = this.resetSearchFilter.bind(this);
    
  }
  
  // Fetch the Employee List
  componentDidMount() { 
    
    const queryParams = queryString.parse(this.props.location.search);
    if( queryParams.organizationId !== undefined) {     
      let filterItem = this.state.filterItem;
      filterItem.filter_organization_id = queryParams.organizationId;
      this.setState({filterItem: filterItem});
    }
    this.EmployeeList(this.state.filterItem);
  }
  /*Employee List API*/
  EmployeeList(filterItem={}) {
    let stroreWalkQuery = "";
    if(filterItem.filter_organization_id !== undefined && filterItem.filter_organization_id !== "" ) 
      stroreWalkQuery += (stroreWalkQuery !=="" ) ? "&organizationId="+filterItem.filter_organization_id: "?organizationId="+filterItem.filter_organization_id;
    if(filterItem.custom_search !== undefined && filterItem.custom_search !== "" ) 
      stroreWalkQuery += (stroreWalkQuery !=="" ) ? "&keyword="+filterItem.custom_search: "?keyword="+filterItem.custom_search;
    this.setState( { loading: true}, () => {
      commonService.getAPIWithAccessToken('employee'+stroreWalkQuery)
        .then( res => {
          
           
          if ( undefined === res.data.data || !res.data.status ) {
            this.setState( { loading: false } );
            toast.error(res.data.message);
            return;
          }   

          this.setState({loading:false, EmployeeList: res.data.data});     
         
        } )
        .catch( err => {         
          if(err.response !== undefined && err.response.status === 401) {
            localStorage.clear();
            this.props.history.push('/login');
          }
          else {
            this.setState( { loading: false } );
            toast.error(err.message);
          }
        } )
    } )
  }

  
  filterEmployeeList(){
    const filterItem = this.state.filterItem;
    this.EmployeeList(filterItem);
  }
  /* Submit Form Handler*/
  submitHandler (event) {
    event.preventDefault();
    event.target.className += " was-validated";
    if(this.validateForm())
      return false;
    this.setState( { formProccessing: true}, () => {
      const formInputField = this.state.formField;
      let statusText = commonService.getStatusVal(formInputField.status)
    
      const formData = {
        "email": formInputField.email,
        "firstName": formInputField.firstName, 
        "lastName": formInputField.lastName, 
        "phoneNumber": formInputField.phoneNumber, 
        "address": formInputField.address, 
        "roleName": formInputField.role, 
        "city": formInputField.city, 
        "state": formInputField.state, 
        "country": formInputField.country, 
        "postalCode": formInputField.postalCode, 
        "employeeName": formInputField.firstName,
        
        "status": statusText
      };
      const rowIndex = this.state.rowIndex;
      if(rowIndex > -1) {
        const employeeInfo = this.state.EmployeeList[rowIndex];

        commonService.putAPIWithAccessToken('employee/'+employeeInfo.profileId, formData)
        .then( res => {
          
           
          if ( undefined === res.data.data || !res.data.status ) {
           
            this.setState( { formProccessing: false} );
            toast.error(res.data.message);
            return;
          } 
          
          this.setState({ modal: false, formProccessing: false});
          toast.success(res.data.message);
          this.EmployeeList(this.state.filterItem);
         
        } )
        .catch( err => {         
          if(err.response !== undefined && err.response.status === 401) {
            localStorage.clear();
            this.props.history.push('/login');
          }
          else
            this.setState( { formProccessing: false } );
            toast.error(err.message);
        } )
      }
      else{
        commonService.postAPIWithAccessToken('employee', formData)
        .then( res => {
         
          if ( undefined === res.data.data || !res.data.status ) { 
            this.setState( { formProccessing: false} );
            toast.error(res.data.message);
            return;
          } 
          
          this.setState({ modal: false});
          toast.success(res.data.message);
          this.EmployeeList(this.state.filterItem);
         
        } )
        .catch( err => {         
          if(err.response !== undefined && err.response.status === 401) {
            localStorage.clear();
            this.props.history.push('/login');
          }
          else
            this.setState( { formProccessing: false } );
            toast.error(err.message);
        } )
      }
    } );
    
  };
  /* Input Field On changes*/
  changeHandler = event => {
    const name = event.target.name;
    const value = event.target.value;
    const formField = this.state.formField
    formField[name] = value;
    this.setState({ formField: formField });
  };

  changeFilterHandler = event => {
    const name = event.target.name;
    const value = event.target.value;
    const filterItem = this.state.filterItem
    filterItem[name] = value;
    this.setState({ filterItem: filterItem });
  };
  
  /* Validate Form Field */
  
  
  /* Validate Form */
  validateForm() {
    let formValid = false;
    let formField = this.state.formField; 
    let errors = {};
    var pattern = new RegExp(/^(("[\w-\s]+")|([\w-]+(?:\.[\w-]+)*)|("[\w-\s]+")([\w-]+(?:\.[\w-]+)*))(@((?:[\w-]+\.)*\w[\w-]{0,66})\.([a-z]{2,6}(?:\.[a-z]{2})?)$)|(@\[?((25[0-5]\.|2[0-4][0-9]\.|1[0-9]{2}\.|[0-9]{1,2}\.))((25[0-5]|2[0-4][0-9]|1[0-9]{2}|[0-9]{1,2})\.){2}(25[0-5]|2[0-4][0-9]|1[0-9]{2}|[0-9]{1,2})\]?$)/i);
    if(formField.firstName === undefined || formField.firstName.trim() === ""){
      formValid = true;
      errors['firstName'] = "First name field required!";
    }
    
    if(formField.email === undefined || formField.email.trim() === ""){
      formValid = true;
      errors['email'] = "Email field required!";
    }
    else if(!(pattern.test(formField.email))){
      formValid = true;
      errors['email'] = "Invalid email format";
    }
    if(formField.status === undefined || formField.status.trim() === "" ){
      formValid = true;
      errors['status'] = 'Status field required';
    }
    this.setState({errors:  errors});
    return formValid;
    
  }
  /* Set Error Class*/
  errorClass(error) {
    return(error.length === 0 ? '' : 'has-error');
  }

  toggle = () => {
    this.setState({
      modal: !this.state.modal,
      rowIndex: -1,
      formValid: false,
      formProccessing : false,
      formField: {email: '', firstName: '', lastName:'', phoneNumber: '', address: '', city: '', state: '', country: '', postalCode: '', role: '' },
      errors: {email: '', employee_name: '', role: '', error: ''}
    });
  }
  /* Edit Employee*/
  handleEditEmployee(rowIndex){
      const employeeInfo = this.state.EmployeeList[rowIndex];
      let status = "Active"
      status = commonService.getStatus(employeeInfo.status);
      const formField = {
        organizationId: employeeInfo.organizationAuthId,        
        email: employeeInfo.email, 
        firstName: employeeInfo.firstName,
        lastName: employeeInfo.lastName, 
        phoneNumber: employeeInfo.phoneNumber, 
        address: employeeInfo.address, 
        city: employeeInfo.city, 
        state: employeeInfo.state, 
        country: employeeInfo.country, 
        postalCode: employeeInfo.postalCode, 
        role: employeeInfo.roleName,
        status: status };
      this.setState({rowIndex: rowIndex, formField: formField, formProccessing : false, modal: true, formValid: true});
  }
  /* Delete Employee*/
  handleDeleteEmployee(rowIndex){
   
    const employeeInfo = this.state.EmployeeList[rowIndex];
    
    this.setState( { loading : true}, () => {   
      commonService.deleteAPIWithAccessToken('employee', {employeeId: employeeInfo.profileId})
      .then( res => {
        
         
        if ( undefined === res.data.data || !res.data.status ) {
         
          this.setState( { loading : false} );
          toast.error(res.data.message);
          return;
        } 
        
        this.setState({ modal: false, loading : false});
        toast.success(res.data.message);
        this.EmployeeList();
       
      } )
      .catch( err => {         
        if(err.response !== undefined && err.response.status === 401) {
          localStorage.clear();
          this.props.history.push('/login');
        }
        else
          this.setState( { loading : false } );
          toast.error(err.message);
      } )
    } );
   
    
  }
 
  resetSearchFilter() {
    this.setState({filterItem: {filter_organization_id: "", custom_search: "" }});
    this.EmployeeList();
  }
  render() {

    const { EmployeeList, loading, modal, formProccessing, errors } = this.state;     
    let loaderElement = '';
    if(loading)        
      loaderElement = <Loader />

    const processingBtnText = <>Submit <i className="fa fa-spinner"></i></>;
    
    return (
      <div className="dashboard-section">
        {loaderElement}
        <Card className="vd-card">
          <CardHeader>
            <div className="d-flex align-items-center">
              <div className="mr-auto">
                <h4 className="card-title"><i class="fa fa-user"></i> Manage Employee User</h4>
              </div>
              <div className="add-option-info">
                <Button className="btn-add" type="button" onClick={this.toggle}><i className="fa fa-plus"></i> Add New</Button>
              </div>
            </div>
          </CardHeader>
          <CardBody className="item-list-section">
            <div className="Search-filter">
              <div className="row">
                  
                  
                  <Col md={"3"}>
                    <FormGroup> 
                      <Label htmlFor="filter_organization_id">Search By Email/ Name</Label>            
                      <Input type="text" placeholder="Search By Email/ Name" id="custom_search" name="custom_search" value={this.state.formField.custom_search} onChange={this.changeFilterHandler} />
                    </FormGroup>  
                  </Col>
                  <Col md={"1"} className="p-0">
                    <FormGroup> 
                      <Label>&nbsp;</Label>
                      <Button color=" search-c-change" type="button" size="lg" onClick={this.filterEmployeeList}><i className="fa fa-search"></i></Button>&nbsp;
                      <Button color=" reset-c-change" type="reset" size="lg" onClick={this.resetSearchFilter}><i className="fa fa-refresh"></i></Button>
                    </FormGroup>             
                  </Col>
              </div>
          </div>
            <div className="card-table table-responsive">
              <EmployeeData data={EmployeeList} editEmployeeAction={this.handleEditEmployee} deleteEmployeeAction={this.handleDeleteEmployee} dataTableLoadingStatus = {this.state.loading} />
                  
            </div>
          </CardBody>
        </Card>
        <Modal isOpen={modal} toggle={this.toggle} className="full-width-modal-section employee-modal">
          <ModalHeader toggle={this.toggle}>Company Employee User</ModalHeader>
          <Form onSubmit={this.submitHandler} noValidate>
            <ModalBody>
              
              <Row>
                 
                <Col md={"6"}>  
                  <FormGroup> 
                    <Label htmlFor="firstName">First Name</Label>            
                    <Input type="text" placeholder="First Person *" id="firstName" name="firstName" value={this.state.formField.firstName} onChange={this.changeHandler} required />
                    <div className="invalid-feedback">
                      {errors['firstName']}
                    </div>
                  </FormGroup>
                </Col>
                <Col md={"6"}>  
                  <FormGroup> 
                    <Label htmlFor="lastName">Last Name</Label>            
                    <Input type="text" placeholder="Last Person" id="lastName" name="lastName" value={this.state.formField.lastName} onChange={this.changeHandler} />
                  </FormGroup>
                </Col>
                
                <Col md={"6"}>  
                  <FormGroup> 
                    <Label htmlFor="email">Email</Label>            
                    <Input type="text" placeholder="Email *" id="email" name="email" value={this.state.formField.email} onChange={this.changeHandler} required />
                    <div className="invalid-feedback">
                      {errors['email']}
                    </div>
                  </FormGroup>
                </Col>
                
                <Col md={"6"}>  
                  <FormGroup> 
                    <Label htmlFor="phoneNumber">Phone Number</Label>            
                    <Input type="number" placeholder="Phone Number" id="phoneNumber" name="phoneNumber" value={this.state.formField.phoneNumber} onChange={this.changeHandler}  />
                  </FormGroup>
                </Col>
                <Col md={"6"}>  
                  <FormGroup> 
                    <Label htmlFor="address">Street Address </Label>            
                    <Input type="text" placeholder="Street Address" id="address" name="address" value={this.state.formField.address} onChange={this.changeHandler}  />
                  </FormGroup>
                </Col>
                
                <Col md={"6"}>  
                  <FormGroup> 
                    <Label htmlFor="city">City </Label>            
                    <Input type="text" placeholder="City" id="city" name="city" value={this.state.formField.city} onChange={this.changeHandler}  />
                  </FormGroup>
                </Col>
                <Col md={"6"}>  
                  <FormGroup>
                    <Label htmlFor="state">State</Label>            
                    <Input type="text" placeholder="State" id="state" name="state" value={this.state.formField.state} onChange={this.changeHandler} />
                  </FormGroup>
                </Col>
                <Col md={"6"}>  
                  <FormGroup> 
                    <Label htmlFor="postalCode">Postal Code </Label>            
                    <Input type="text" placeholder="Postal Code" id="postalCode" name="postalCode" value={this.state.formField.postalCode} onChange={this.changeHandler}  />
                  </FormGroup>
                </Col>
                <Col md={"6"}>  
                  <FormGroup> 
                    <Label htmlFor="country">Country </Label>            
                    <Input type="text" placeholder="Country" id="country" name="country" value={this.state.formField.country} onChange={this.changeHandler} />
                  </FormGroup>
                </Col>
                <Col md={"6"}>
                  <FormGroup>
                    <Label htmlFor="template_status">Status</Label>            
                    <Input type="select" placeholder="Status *" invalid={errors['status'] !== undefined && errors['status'] !== ""} id="status" name="status" value={this.state.formField.status} onChange={this.changeHandler} required >
                      <option value="">Select Status</option>
                      <option value="Active">Active</option>
                      <option value="Inactive">Inactive</option>
                    </Input>
                    <div className="invalid-feedback">
                      {errors['status']}
                    </div>
              </FormGroup>
                </Col>
              </Row>
            </ModalBody>
            <ModalFooter>
              <Button color="primary" disabled={formProccessing} type="submit">{formProccessing ? processingBtnText : 'Submit' }</Button>
              <Button color="secondary" onClick={this.toggle}>Cancel</Button>
            </ModalFooter>
          </Form>
        </Modal>
      </div>

    )
  }
}



export default Employee;
