import React, { Component } from 'react';
import {
  Row, 
} from 'reactstrap';
import commonService from '../../../core/services/commonService';
import Loader from '../../Loader/Loader';
import './Dashboard.css';

class Dashboard extends Component {
  constructor(props) {
    super( props );

    this.state = {
      loading: false,
      dashBoardStats: { totalEmployeeUser: 0,
          totalCompanyUser: 0,
          totalCases: 0,
          totalNewCases: 0,
          totalInProgressCases: 0,
          totalClosedCases: 0, },
      
    };
  }

  componentDidMount() {     
    this.dashboardData({});
   
  }

  /* Get Dashboard data from API */
  dashboardData() {
    commonService.getAPIWithAccessToken('dashboard')
    .then( res => {
      if( undefined === res.data.data || !res.data.status ){
        this.setState( {loading: false });
        return;
      }
      const dashData = res.data.data;
      this.setState({ loading:false, dashBoardStats:dashData  })
    }
    )
  }

  

  
  loading = () => <div className="animated fadeIn pt-1 text-center">Loading...</div>

  render() {
    const { loading }  = this.state;
    let loaderElement = '';
    if(loading)        
      loaderElement = <Loader />

    return (
      <div className="dashboard-section container dashboard-overview vd-card card mt-4">
        {loaderElement}
        <Row>
          
        </Row>
        
        <div class="row">
          <div class="col-lg-12">
            <div class="user-H">
              <h5>User Overview</h5>
              <hr/>
            </div>
          </div>
          <div class="col-lg-4 col-md-6 col-sm-6">
            <div class="card card-stats">
              <div class="card-header card-header-warning card-header-icon">
                <div class="card-icon">
                   <i class="fa fa-user"></i>
                </div>
                <p class="card-category">Total Employee User</p>
                <h3 class="card-title">
                  {this.state.dashBoardStats.totalEmployeeUser}
                </h3>
              </div>
            </div>
          </div>

          <div class="col-lg-4 col-md-6 col-sm-6">
            <div class="card card-stats">
              <div class="card-header card-header-success card-header-icon">
                <div class="card-icon">
                   <i class="fa fa-group"></i>
                </div>
                <p class="card-category">Total Company user</p>
                <h3 class="card-title">{this.state.dashBoardStats.totalCompanyUser}</h3>
              </div>
            </div>
          </div>
        </div>
        <div class="row mt-5">
          <div class="col-lg-12">
            <div class="cases-H">
              <h5>Cases Overview</h5>
              <hr/>
            </div>
          </div>
          <div class="col-lg-4 col-md-6 col-sm-6">
            <div class="card card-stats">
              <div class="card-header card-header-info card-header-icon">
                <div class="card-icon">
                  <i class="fa fa-file-text" aria-hidden="true"></i>
                </div>
                <p class="card-category">Total registered cases</p>
                <h3 class="card-title">{this.state.dashBoardStats.totalCases}</h3>
              </div>
            </div>
          </div>

          <div class="col-lg-4 col-md-6 col-sm-6 ">
            <div class="card card-stats">
              <div class="card-header card-header-info-closed card-header-icon">
                <div class="card-icon">
                  <i class="fa fa-file-text" aria-hidden="true"></i>
                </div>
                <p class="card-category">Closed cases</p>
                <h3 class="card-title">{this.state.dashBoardStats.totalClosedCases}</h3>
              </div>
            </div>
          </div>

          <div class="col-lg-4 col-md-6 col-sm-6 ">
            <div class="card card-stats">
              <div class="card-header card-header-info-pending card-header-icon">
                <div class="card-icon">
                  <i class="fa fa-file-text" aria-hidden="true"></i>
                </div>
                <p class="card-category"> Investigation Inprogress Cases</p>
                <h3 class="card-title">{this.state.dashBoardStats.totalInProgressCases}</h3>
              </div>
            </div>
          </div>

          <div class="col-lg-4 col-md-6 col-sm-6 mt-5">
            <div class="card card-stats">
              <div class="card-header card-header-info-NewCases card-header-icon">
                <div class="card-icon">
                  <i class="fa fa-file-text" aria-hidden="true"></i>
                </div>
                <p class="card-category">New Cases</p>
                <h3 class="card-title">{this.state.dashBoardStats.totalNewCases}</h3>
              </div>
            </div>
          </div>

        </div> 
      </div>
    );
  }
}

export default Dashboard;
