import React, { Component } from 'react';
import  { Link } from 'react-router-dom';
import MUIDataTable from "mui-datatables";
//import commonService from '../../../core/services/commonService';
const getStatusText = (status) => {
  let statusText = 'New';
  if(status === 2)
    statusText = 'Investigation in progress';
  else if(status === 3)
    statusText = 'Closed';
  return statusText;

}
class RegisteredCasesData extends Component {
  
  constructor(props){
    super(props);   
    this.state = {
      buttonProcessing: false,
      rowIndex: '',
      dataTableItem: []
    };
    
  }
  componentDidMount() {   
  }
  

  deleteRegisteredCaseItem(rowIndex){    
    this.props.deleteCasesAction(rowIndex);
  }

  changeRegisteredCaseItemInvestigator(rowIndex){
    this.props.changeRegisteredInvestigatorAction(rowIndex);
  }

  render() {
    
    let rowsItem = [];    
    for(const [i, casesInfo] of this.props.data.entries()){
      console.log(i);

      let orgInfo = {   
        caseId: casesInfo.caseAutoId,      
        caseTitle: `${casesInfo.caseTitle}`,
        caseTypeName: casesInfo.caseTypeName,
        threatType: Array.isArray(casesInfo.threatType) ? casesInfo.threatType.join(',') : "",
        employeeName: casesInfo.employeeName || " ",
        assignUserName: casesInfo.assignUserName || "",
        status: getStatusText(casesInfo.status),   
        action: "",       
      }      
      rowsItem.push(orgInfo);
    }      
    
    const columns = [ 
      {
        label: 'Case Id',
        name: 'caseId',
      },     
      
      {
        label: 'Case Title',
        name: 'caseTitle',
      },
      {
        label: 'Case Type',
        name: 'caseTypeName',
      },
      {
        label: 'Case Threat',
        name: 'threatType',
      },
      {
        label: 'Case Registred By',
        name: 'employeeName',
      },
      {
        label: 'Assigned Company User',
        name: 'assignUserName',
      },
      {
        label: 'Status',
        name: 'status',
      },
      {
        label: 'Action',
        name: 'action',
        options: {
          filter: true,
          customBodyRender: (value, tableMeta, updateValue) => {
            let i = tableMeta.rowIndex;
            let rowsItem = this.props.data[i] 
            return (
             <p>
              <Link className="btn-view" to ={`/user/registered-cases/${rowsItem.caseId}`}><i className="fa fa-eye"></i></Link>
              <button className="btn-edit" disabled={this.state.buttonProcessing} onClick={() => {if( window.confirm('Are you sure you wish to change assign company user?'))
              this.changeRegisteredCaseItemInvestigator(i)}}><i className="fa fa-user"></i></button>
              <button className="btn-delete" disabled={this.state.buttonProcessing} onClick={() => {if( window.confirm('Are you sure you wish to delete this registered case?'))
              this.deleteRegisteredCaseItem(i)}}><i className="fa fa-trash"></i></button></p>
            );
          },
        }
      },
    ];
    const options = {
      search: true,viewColumns: false,
      filter: false,
      searchOpen: false,
      print: false,
      download: true,
      responsive: 'stacked',
      selectableRows: 'none',
      textLabels: {
        body: {
          noMatch: this.props.dataTableLoadingStatus ? "Processing........" : "",
          toolTip: "Sort",
          columnHeaderTooltip: column => `Sort for ${column.label}`
        },
      },
      fixedHeaderOptions: { xAxis: false, yAxis: false }

    };
    
    return (
      <MUIDataTable
        title={"Registered Cases"}
        data={rowsItem}
        columns={columns}
        options={options}
      />
    );
  }
}

export default RegisteredCasesData;