import React from "react";
import { Col, Row } from "reactstrap";
import { Link } from 'react-router-dom';
import Loader from '../../Loader/Loader';

import  SubscriptionPlan  from './SubscriptionPlan';

class BuySubscription extends React.Component {
  constructor(props){
    super(props);
    this.state = {      
      loading: false,   
    } 
  }

  // Fetch the subCategory List
  componentDidMount() {
    
  }

  

  

  render() {
    const { loading } = this.state;    
    
    let loaderElement ='';
    if(loading)
      loaderElement = <Loader />
   
      return (
        <div className="main-content"> 
            <section className="Subscription-info-section">
                <div className="container-fluid">
                    <Row>
                        <Col md="12">
                            
                        </Col>
                    </Row>
                    
                    <div className="heading-title">
                      <h2>Subscription Plan</h2>
                    </div>
                    <Row className="align-items-center">
                        
                        <Col md="12" lg="12">
                          {loaderElement}  
                          <div className="planList">  
                            <div className="container-fluid bg-gradient p-5">
                               <div className="plan-section">
                                <div className="OfferTopHeading">
                                  <div className="OfferContentFirst">
                                    
                                  </div>
                                  <div className="OfferContentSecond">
                                    <p>
                                      </p><h4>How it works</h4>
                                      
                                      <ul>
                                        <li>Select your preferred plan.</li>
                                        <li>Easily pay through Stripe.</li>
                                        <li>Avail the benefits as per selected plan.</li>
                                      </ul>
                                    <p></p>
                                    <p className="contactLine">If you have any questions about our subscription plans, feel free to <Link to="/contact-us" title="Contact Us"> contact us </Link> </p>
                                  </div>
                                </div>            
                                <SubscriptionPlan  propHistory={this.props.history} hideBuyButton = {false} />
                              </div>
                            </div>  
                          </div>
                        </Col>
                        
                      </Row>
                </div>
            </section>  
        </div>
        
      );
    
    
  }
}



export default BuySubscription;
