import Dashboard from '../views/User/UserDashboard/Dashboard';
import MyProfile from '../views/User/MyProfile/MyProfile';
import ChangePassword from '../views/User/MyProfile/ChangePassword';
import CompanyUser from '../views/User/CompanyUser/CompanyUser';
import Employee from '../views/User/Employee/Employee';
import RegisteredCases from '../views/User/RegisteredCases/RegisteredCases';
import RegisteredCasesDetails from '../views/User/RegisteredCases/RegisteredCasesDetails';
import Expense from '../views/User/Expense/Expense';
import Mileage from '../views/User/Mileage/Mileage';
import BuySubscription from '../views/User/Subscription/BuySubscription';
import CurrentSubscription from '../views/User/Subscription/CurrentSubscription';

const userRoutes = [
  { path: '/user/', exact: true, name: 'Home' },
  { path: '/user/dashboard', exact: true, name: 'Dashboard', component: Dashboard },  
  { path: '/user/my-profile', exact: true,  name: 'Profile', component: MyProfile }, 
  { path: '/user/change-password', exact: true,  name: 'Change Password', component: ChangePassword },
  { path: '/user/company-user', exact: true, name: 'Manage Compnay Users', component: CompanyUser },
  { path: '/user/company-employee', exact: true, name: 'Manage Employee User', component: Employee },
  { path: '/user/registered-cases', exact: true, name: 'Manage Registered Cases', component: RegisteredCases },
  { path: '/user/registered-cases/:registredCaseId', exact: true, name: 'Registered Case Details', component: RegisteredCasesDetails },
  { path: '/user/expense', exact: true, name: 'Manage Expense', component: Expense },
  { path: '/user/subscription', exact: true, name: 'Subscription', component: BuySubscription },
  { path: '/user/subscription/info', exact: true, name: 'Active Subscription', component: CurrentSubscription },
];

export default userRoutes;
