import React , { Component } from 'react';
//import { Button, Input, FormGroup, FormFeedback, Label } from 'reactstrap';
//import { toast } from 'react-toastify';
//import commonService from '../../core/services/commonService';
import './pdfstyle.css'


class PdfContainer extends Component {
  
  constructor(props){
    super(props);
    this.state = {
      modal: false, 
      share_email: "",
      email_error: "",
      loading: false    

    } 
   this.bodyRef = React.createRef();
   
  }
 
  
  createPdf = () => this.props.createPdf(this.bodyRef.current);   
  render() {
    
    return (
      <section className="pdf-container">
        <section className="pdf-toolbar pdf-toolbar-padding">
          <button onClick={this.createPdf} className="pdf-download-button pull-right"><i className="fa fa-download"></i> Download PDF</button>
          

        </section>
        <section className="pdf-body" style= {{padding: "20px"}} ref={this.bodyRef}>
          
          {this.props.children}
        </section>
        
      </section>
    )
  }
}
export default PdfContainer;