import React from "react";
import { Row, Col, Modal, ModalHeader, ModalBody, ModalFooter, Button, Form, Input, FormGroup, FormFeedback, Label,} from 'reactstrap';
import { toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import commonService from '../../../core/services/commonService';

import "./SubscriptionPlan.css";
const proPlanImage = `${process.env.PUBLIC_URL}/images/p3.jpg`;
const basicPlanImage = `${process.env.PUBLIC_URL}/images/p2.jpg`;
const trialPlanImage = `${process.env.PUBLIC_URL}/images/p1.jpg`;
const teamPlanImage = `${process.env.PUBLIC_URL}/images/p4.jpg`;
const getPlanImage = (planName) => {
  if(planName === "pro")
    return proPlanImage;
  else if(planName === "basic")
    return basicPlanImage;
  else
    return trialPlanImage;
}
//const stripePromise = loadStripe('pk_test_JJ1eMdKN0Hp4UFJ6kWXWO4ix00jtXzq5XG');

class SubscriptionPlan extends React.Component {
  constructor(props){
    super(props);
    
    const year = (new Date()).getFullYear();
    this.years = Array.from(new Array(10),(val, index) => index + year);
    this.state = {  
      loading: false,
      paymentProcess: false,      
      planId: "",
      planList: [],
      authId: "",
      activePlanInfo: [],
      termCondtionAccepted: {},
      activePlanType: 1,
      cvc: '',
      message: '',
      expYear: '',
      expMonth: '',
      cardNumber: '',
      formProcess: false,
      modal: false,
      formProccessing: false,
      formErrors: {},
      settingsData: {},
      referal_code: "",
      alreadySubscribe: false,
      alreadyUsedReferal: false,
      displayReferal: false,
      fullName: "",
      address_line_1: "",
      address_line_2: "",
      city: "",
      state: "",
      country: "",
      postal_code: "",
      phone:""
    }    
    this.buySubscription = this.buySubscription.bind(this); 
    
    this.subscriptionPlanList = this.subscriptionPlanList.bind(this); 
    this.cancelSubscription = this.cancelSubscription.bind(this);
    this.acceptTermCondtion = this.acceptTermCondtion.bind(this);
  }

 
  componentDidMount() {    
    this.subscriptionPlanList();
    if(!window.document.getElementById('stripe-script')) {
      var s = window.document.createElement("script");
      s.id = "stripe-script";
      s.type = "text/javascript";
      s.src = "https://js.stripe.com/v2/";
      s.onload = () => {
        //window['Stripe'].setPublishableKey('pk_test_cstJhEQ7MKZDVDOjwnyBHkir00BGfqW0cT');
        window['Stripe'].setPublishableKey('pk_test_H3p9Wd37m3sZUU5IXI9D5GCZ');
      }
      window.document.body.appendChild(s);
    }
  }

  
  acceptTermCondtion(planInfo, event) {
    
    let status = false;
    let planId = planInfo.planId;
    if(event.target.checked) 
      status = true;
    let termCondtionAccepted = this.state.termCondtionAccepted;
    termCondtionAccepted[planId] = status;
    
    //termCondtionAccepted[planVariationId] = status;
    this.setState({termCondtionAccepted: termCondtionAccepted})
  }
  subscriptionPlanList(querySnapshot){
    this.setState( { loading: true}, () => {
      commonService.getAPIWithAccessToken('subscription')
        .then( res => {
           
          if ( undefined === res.data.data || !res.data.status ) {
            this.setState( { loading: false } );
            toast.error(res.data.message);
            return;
          }   

          this.setState({loading:false, planList: res.data.data});     
         
        } )
        .catch( err => {         
          if(err.response !== undefined && err.response.status === 401) {
            localStorage.clear();
            this.props.history.push('/login');
          }else {
            this.setState( { loading: false } );
            toast.error(err.message);
          }
        } )
    });
  }
  

  cancelSubscription(planInfo, subscriptionInfo) {
    if(this.state.termCondtionAccepted[planInfo.planId] === undefined  || !this.state.termCondtionAccepted[planInfo.planId]){
      toast.error("Please accept term and conditions");
      return;
    }
    if( !window.confirm('Are you sure to cancel this subscription?'))
      return false;
   
    this.setState( { loading: true}, () => {
        
    } )
  }

  buySubscription(planInfo) {
    if(planInfo.isPlanActive) {
    	toast.error("Plan already activated on your account!");
    	return;
    }
    
    
    if(this.state.termCondtionAccepted[planInfo.planId] === undefined  || !this.state.termCondtionAccepted[planInfo.planId] ){
      toast.error("Please accept term and conditions");
      return;
    }

    if(commonService.getLocalStorageValue('authId') === undefined || commonService.getLocalStorageValue('authId') === "") {
      this.props.propHistory.push({
        pathname: '/login',
        state: { planId: planInfo.planId }
      });
      return;
    }
    if(commonService.getLocalStorageValue('role') === "admin") {
      toast.error("Admin not allowed to buy subscription");
      return;
    }
    this.setState({modal: true, formProccessing: false, disabled: false, planId: planInfo.planId});
     
  }

  changePlanType = () => {
    if(this.state.activePlanType===1)
      this.setState( { activePlanType: 4 } );
    else
      this.setState( { activePlanType: 1 } );
  }

  toggle = () => {
    this.setState({
      modal: !this.state.modal,
      cvc: '',
      message: '',
      expYear: '',
      expMonth: '',
      cardNumber: '',
      formProcess: false,
      
    });
  }

  handleCardChange = (evt) => {
    this.setState({
      [evt.target.name] : evt.target.value
    });
  }

  pay = (e) => {
 
    e.preventDefault();    
    e.target.className += " was-validated";
    if(this.validateForm() === false)
      return false;
    this.setState({
      formProccessing: true
    });
     
    window.Stripe.card.createToken({
      number: this.state.cardNumber,
      exp_month: this.state.expMonth,
      exp_year: this.state.expYear,
      cvc: this.state.cvc
    }, (status, response) => {
       
      if (status === 200) {
         const tokenId = response.id;
         commonService.postAPIWithAccessToken('subscription/buy', {tokenId: tokenId, planId: this.state.planId, planType: this.state.activePlanType})
          .then( res => {           
            if ( undefined === res.data.data || !res.data.status ) { 
              this.setState( { formProccessing: false} );
              toast.error(res.data.message);
              return;
            } 
            this.setState({ modal: false});
            toast.success(res.data.message);
          } )
          .catch( err => {         
            if(err.response !== undefined && err.response.status === 401) {
              localStorage.clear();
              this.props.history.push('/login');
            }else{
              this.setState( { formProccessing: false } );
              toast.error(err.message);
            }
          } )
 
      } else {
        
        toast.error(response.error.message);
        this.setState({
          formProccessing: false
        });
      }
    });
    
  }

  
  validateForm() {
    
    const formErrors = this.state.formErrors;
    
    let formValid = true;
    if(this.state.cardNumber === ""){
      formErrors['cardNumber'] = "Card number required";
      formValid = false;
    }
    if(this.state.expMonth === ""){
      formErrors['expiryMonth'] = "Expiry month required";
      formValid = false;
    }
    if(this.state.expYear === ""){
      formErrors['expiryYear'] = "Expiry year required";
      formValid = false;
    }
    if(this.state.cvc === ""){
      formErrors['cardCVC'] = "CVC required";
      formValid = false;
    }
    if(this.state.planId === "" ){
      toast.error("Please select subscription plan")
      formValid = false;
    }
    

    this.setState({formErrors: formErrors})
    return formValid;
  }

  handleReferalChange = event =>{
      this.setState({referal_code: event.target.value.toUpperCase()});
  }


  render() {
    
    const { planList, paymentProcess, activePlanType, formProccessing, formErrors, modal} = this.state;
    let filterPlanList = planList.filter(function(item) { if(activePlanType == 1)  return !item.isAccessThreatModule; else return item.isAccessThreatModule;})
    const processingBtnText = <>Submit <i className="fa fa-spinner"></i></>;
       return (
          
          <>
            <Row className ="m-auto justify-content-center w-85 subscription-plan-section">

              
              <div className="pricing-section">
                <label className={ ( activePlanType===1 ? 'toggler toggler--is-active' : 'toggler' ) } id="filt-monthly">Case</label>
                <div className="toggle">
                    <input type="checkbox" id="switcher" className="check" onClick={ () =>  this.changePlanType() } />
                    <b className="b switch"></b>
                </div>
                <label className={ ( activePlanType===4 ? 'toggler toggler--is-active' : 'toggler' ) } id="filt-yearly">Threat</label>
              </div>
              {filterPlanList.map((planInfo, index) =>
                <SetPlanDetailsInfo key={index} hideBuyButton = {this.props.hideBuyButton} activePlanType = {activePlanType} cancelSubscription= {this.cancelSubscription} activePlanInfo = {this.state.activePlanInfo} planInfo={planInfo} acceptTermCondtion = {this.acceptTermCondtion} planId={this.state.planId} buySubscription={this.buySubscription} paymentProcess= {paymentProcess} />
              )}
              

              <div className="termsCondition">
                <p>* Terms and Conditions apply to the subscription plans.</p>
              </div>
            </Row>
          <Modal isOpen={modal} toggle={this.toggle} className="full-width-modal-section store-modal">
            <ModalHeader toggle={this.toggle}>Plan </ModalHeader>
            <Form onSubmit={this.pay} noValidate>
              <ModalBody>              
                <Row> 
                                
                  
                  
                  <Col md={"6"}>
                    <FormGroup> 
                      <Label htmlFor="plan_name">Card Number</Label>            
                      <Input type="text"  placeholder="Valid Card Number" name="cardNumber" maxLength="16" onChange={this.handleCardChange} required />
                      <FormFeedback>{formErrors.cardNumber}</FormFeedback>
                    </FormGroup>  
                  </Col>
                  <div className="col-md-6">
                      <FormGroup> 
                          <Label htmlFor="plan_name">Expiry Month</Label> 
                          <Input type="select" name="expMonth" className="expMonth"  onChange={this.handleCardChange} required>
                              <option value="">Select Month</option>
                              <option value="1">01</option>
                              <option value="2">02</option>
                              <option value="3">03</option>
                              <option value="4">04</option>
                              <option value="5">05</option>
                              <option value="6">06</option>
                              <option value="7">07</option>
                              <option value="8">08</option>
                              <option value="9">09</option>
                              <option value="10">10</option>
                              <option value="11">11</option>
                              <option value="12">12</option>
                          </Input>
                          <FormFeedback>{formErrors.expiryMonth}</FormFeedback>
                        </FormGroup>
                     
                  </div>
                  <div className="col-md-6 pull-right">
                        <FormGroup> 
                          <Label htmlFor="plan_name">Expiry Year</Label>        
                          <Input type="select" name="expYear" className="expYear" onChange={this.handleCardChange} required>
                              <option value="">Select Year</option>
                              {this.years.map((year, index) =>
                                 <option key={index} value={year}>{year}</option>
                               )}
                                                                           
                          </Input>
                          <FormFeedback>{formErrors.expiryYear}</FormFeedback>
                        </FormGroup>
                  </div>
                  <Col md={"6"}>
                    <FormGroup> 
                      <Label htmlFor="plan_name">CVC CODE</Label>            
                      <Input type="text" name="cvc"  placeholder="CVC" maxLength="4" onChange={this.handleCardChange} required />
                      <FormFeedback>{formErrors.cardCVC}</FormFeedback>
                    </FormGroup>  
                  </Col>
                  

                  
                </Row>
              </ModalBody>
              <ModalFooter>
                <Button color="primary" disabled={formProccessing} type="submit">{formProccessing ? processingBtnText : 'Submit' }</Button>
                <Button color="secondary" onClick={this.toggle}>Cancel</Button>
              </ModalFooter>
            </Form>
          </Modal>
         </>
                 
        );
  }
}
function SetPlanDetailsInfo (props) {
  
  const planInfo = props.planInfo;
  let actionButton = '';
  let buttonTxt = props.paymentProcess ? 'Processing...' : props.activePlanType === 1 ? `Buy for $${planInfo.monthlyAmount}/ per month` : `Buy for $${planInfo.monthlyAmount}/ per month`;
  
  actionButton = <button className="payment-Button"  onClick={() => props.buySubscription(planInfo)} disabled={props.paymentProcess}>{props.paymentProcess && props.planId === planInfo.planId ? buttonTxt: props.activePlanType === 1 ? `Buy for $${planInfo.monthlyAmount}/ per month` : `Buy for $${planInfo.monthlyAmount}/ per month`}</button>
  
  if(planInfo.monthlyPlanInfo === undefined || planInfo.monthlyPlanInfo.id === undefined || planInfo.monthlyPlanInfo.id === "")
    return (<></>);
  return (<div className={`col-md-3 col-lg-3 col-sm-6`}>
                  <div className={`plan-info-item princing-item`} style={{backgroundImage: `url(${getPlanImage('basic')})`,
  backgroundSize: 'cover', backgroundRepeat: 'no-repeat'}}> 
                    <div className="pricing-plan">
                        <h2>{planInfo.planName}</h2>
                        
                        
                        <div className="plan-point-list">
                          <ul className="">
                            {
                              
                              <>
                                <li>{
                                  planInfo.isOrganizationPlan ? `Allow ${planInfo.numberOfCompanyUsers} company users`: `Allow ${planInfo.numberOfEmployeeUsers} employee users`
                                }</li>
                                <li>{props.activePlanType === 1 ? `Case Management` : 'Threat Management'}</li>
                                
                              </>
                            }
                          </ul>
                         
                        </div>
                        {!props.hideBuyButton ? <>
                        {actionButton}
                        <div className="TermsCheckbox">
                          <input type="checkbox" name="term" className="check-term" onChange={(e) => {props.acceptTermCondtion(planInfo, e)}} />
                          <label> I have read and accept the <a href="/terms-of-service" target="_blank" rel="noopener noreferrer" >Terms &amp; Conditions</a> and the <a href="/privacy-policy" target="_blank" rel="noopener noreferrer" >Privacy Policy</a></label>
                        </div> </> : <button className="payment-Button"  disabled={props.paymentProcess}>{props.paymentProcess && props.planId === planInfo.planId ? buttonTxt: props.activePlanType === 1 ? `$${planInfo.monthlyAmount}/ per month` : `$${planInfo.yearlyAmount}/ per year`}</button> }
                        
                    </div>
                    </div>
                </div>);
}
export default SubscriptionPlan;
